import * as React from "react";
import TextField from "@mui/material/TextField";
import Autocomplete from "@mui/material/Autocomplete";
import { graphql, useStaticQuery, navigate } from "gatsby";

export const Search = () => {
  const data = useStaticQuery(graphql`
    query {
      allMarkdownRemark(filter: { frontmatter: { tags: { ne: null } } }) {
        nodes {
          frontmatter {
            tags
          }
        }
      }
    }
  `);

  const allNodes = data?.allMarkdownRemark?.nodes;
  const allTagsArrays: Array<string> = [];

  allNodes?.forEach((node: { frontmatter: any }) => {
    const { frontmatter } = node;
    const tags = [...frontmatter.tags];
    if (!!tags)
      tags?.forEach((tag) => {
        allTagsArrays.push(tag);
      });
  });

  const allTags = allTagsArrays
    .sort()
    .reduce((acc: any, curr: string, idx: number) => {
      if (Object.keys(acc).includes(curr)) acc[curr]++;
      else acc[curr] = 1;
      return acc;
    }, {});

  const tagsToDisplay = Object.keys(allTags).map((tag) => {
    return {
      label: `${tag} (${allTags[tag]})`,
      id: tag,
    };
  });

  return (
    <Autocomplete
      color="primary"
      disablePortal
      sx={{ width: 200 }}
      size="small"
      options={tagsToDisplay}
      renderInput={(params) => (
        <TextField {...params} color="primary" label="Recherche par Tag" />
      )}
      onChange={(event, newValue: { label: string; id: string }) => {
        navigate(`/?tag=${newValue?.id}`);
      }}
    />
  );
};
